import {
  createWatcher
} from './watcherFactory'
import {
  notify
} from './notify'
import {
  proxy
} from './proxy'
import {
  configuration
} from './configuration'
import logger from './log'
import {
  createClass,
  LinkedList,
  map,
  get,
  isNil,
  isPrimitive,
  isArray
} from 'ilos'

const hasOwn = Object.prototype.hasOwnProperty
let lazy = true,
  bindWatcher = '__observi_watcher__'
configuration.register('key.watcher', bindWatcher, 'init', (val) => (bindWatcher = val))
  .register('lazy', lazy, 'init', (val) => lazy = val)

function getOrCreateWatcher(obj) {
  if (hasOwn.call(obj, bindWatcher))
    return obj[bindWatcher]
  return (obj[bindWatcher] = createWatcher(obj))
}

export const Observi = createClass({
  statics: {
    getOrCreateWatcher
  },
  constructor(obj, expr, path) {
    this.obj = obj
    this.expr = expr
    this.path = path
    this.watchers = Array(path.length)
    this.callbacks = this.createCallbacks()
    this.arrayCallback = this.arrayCallback.bind(this)
    this.watch(obj, 0)
    this.watcher = this.watchers[0]
    this.handlers = new LinkedList()
  },
  on(handler) {
    return this.handlers.push(handler)
  },
  un(handler) {
    if (!arguments.length) {
      var size = this.landlers.size()
      this.handlers.clean()
      return size
    } else {
      return this.handlers.remove(handler)
    }
  },
  isListened(handler) {
    return isNil(handler) ? !this.handlers.empty() : this.handlers.contains(handler)
  },
  fire(val, oldVal, eq) {
    var expr = this.expr,
      _proxy = this.watcher.proxy
    this.handlers.each(cb => cb(expr, val, oldVal, _proxy, eq))
  },
  notify() {
    if (this.dirty) {
      this.fire(this.newVal, this.oldVal, this.eq)
      this.dirty = false
    }
  },
  update(val, oldVal, eq) {
    if (lazy) {
      this.newVal = val
      if (this.dirty) {
        if ((eq = proxy.eq(val, this.oldVal)) && isPrimitive(val)) {
          this.dirty = false
          return
        }
        this.eq = eq
      } else {
        this.oldVal = oldVal
        this.eq = eq
        this.dirty = true
        notify(this)
      }
    } else {
      this.fire(val, oldVal, eq)
    }
  },
  createCallbacks() {
    return map(this.path, function(prop, i) {
      return this.createCallback(i)
    }, this)
  },
  createCallback(idx) {
    let path = this.path,
      nextIdx = idx + 1,
      rpath = (path.length - nextIdx) && path.slice(nextIdx)

    return (attr, val, oldVal, watcher, eq) => {
      if (rpath) {
        if (eq) return

        // unwatch & get old value
        if (oldVal) {
          oldVal = proxy.obj(oldVal)
          this.unwatch(oldVal, nextIdx)
          oldVal = get(oldVal, rpath)
        } else {
          oldVal = undefined
        }
        // watch & get new value
        if (val) {
          if (proxy.isEnable()) { // reset value on up-level object
            val = proxy.obj(val)
            var w = this.watch(val, nextIdx),
              i = 0,
              obj = this.obj
            while (i < idx) { // find up-level object
              obj = proxy.obj(obj[path[i++]])
              if (!obj) return
            }
            obj[path[idx]] = w.proxy
          } else {
            this.watch(val, nextIdx)
          }
          val = get(val, rpath)
        } else {
          val = undefined
        }
        if ((eq = proxy.eq(val, oldVal)) && isPrimitive(val))
          return
      } else {
        this.unwatchArray()
        this.watchArray(val)
      }
      this.update(val, oldVal, eq)
    }
  },
  arrayCallback(attr, val, oldVal, watcher) {
    this.update(watcher.proxy, watcher.proxy, true)
  },
  watch(obj, idx) {
    let path = this.path,
      attr = path[idx],
      watcher = this.watchers[idx] || (this.watchers[idx] = getOrCreateWatcher(obj)),
      val

    watcher.setter(attr, this.callbacks[idx])

    if (val = obj[attr]) {
      if (++idx < path.length) {
        if (proxy.isEnable()) {
          obj[attr] = this.watch(proxy.obj(val), idx).proxy
        } else {
          this.watch(val, idx)
        }
      } else {
        this.watchArray(val)
      }
    }
    return watcher
  },
  unwatch(obj, idx) {
    let watcher = this.watchers[idx]
    if (watcher) {
      var path = this.path,
        attr = path[idx],
        val

      watcher.unsetter(attr, this.callbacks[idx])
      this.watchers[idx] = undefined
      if (val = obj[attr]) {
        if (++idx < path.length) {
          this.unwatch(proxy.obj(val), idx)
        } else {
          this.unwatchArray()
        }
      }
    }
    return watcher
  },
  watchArray(val) {
    if (isArray(val))
      (this.arrayWatcher || (this.arrayWatcher = getOrCreateWatcher(val))).setter('length', this.arrayCallback)
  },
  unwatchArray() {
    if (this.arrayWatcher) {
      this.arrayWatcher.unsetter('length', this.arrayCallback)
      this.arrayWatcher = undefined
    }
  }
})
